import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import Navigation from './components/Navigation/Navigation'
import Logo from './components/Logo/Logo'
import ImageLinkForm from './components/ImageLinkForm/ImageLinkForm'
import Rank from './components/Rank/Rank'
import Particles from 'react-particles-js';
import FaceRecognition from './components/FaceRecognition/FaceRecognition'
import Signin from './components/Signin/Signin'
import Register from './components/Register/Register'

import Clarifai from 'clarifai';

const app = new Clarifai.App({
 apiKey: 'd582c5e3521f45af8f7e538482b1ef24'
});
const particlesOptions = {

                particles: {
                  number:{
                    value: 200,
                    density: {
                      enable: true,
                      value_area: 2000
                    }
                  }
                }
              }

class App extends Component {
  constructor(){
    super();
    this.state = {
      input:'',
      imageUrl: '',
      box: [],
      route: 'signin',
      isSignedIn: false,
      user: {
        "id": "",
        "name": "",
        "email": "",
        "entries": 0,
        "joined": ""


      }
     
    }
  }


  // componentDidMount(){
  //   fetch('https://infinite-refuge-16426.herokuapp.com')
  //   .then(response => response.json())
  //   .then(data => console.log(data))
  // }

  loadUser = (data) => {
    this.setState({user:{
        "id": data.id,
        "name": data.name,
        "email": data.email,
        "entries": data.entries,
        "joined": data.joined
      }
    })
  }

  calculateFaceLocation = (data) => {
    const clarifaiFace = data.outputs[0].data.regions[0].region_info.bounding_box;
    const image = document.getElementById('inputImage');
    const width = Number(image.width);
    const height = Number(image.height);
    
    let faceRegionArray = []
    let boundingBoxArray = []


    data.outputs[0].data.regions.map((faceRegion)=>faceRegionArray.push(faceRegion));
    

    for(let i=0; i< data.outputs[0].data.regions.length; i++){
            let faceBoundingBox = {};
            
            faceBoundingBox.id = i;
            faceBoundingBox.leftCol = faceRegionArray[i].region_info.bounding_box.left_col * width
            faceBoundingBox.topRow = faceRegionArray[i].region_info.bounding_box.top_row * height
            faceBoundingBox.rightCol = width - faceRegionArray[i].region_info.bounding_box.right_col * width
            faceBoundingBox.bottomRow = height -  faceRegionArray[i].region_info.bounding_box.bottom_row * height
            faceBoundingBox.name = faceRegionArray[i].data.concepts[0].name;

            boundingBoxArray.push(faceBoundingBox);
            console.log(data)
            console.log(data.outputs[0].data.regions.length)
            console.log(faceBoundingBox)
           
          }
     // leftCol: clarifaiFace.left_col * width,
    // topRow: clarifaiFace.top_row * height,
    // rightCol: width - (clarifaiFace.right_col * width),
    // bottomRow: height - (clarifaiFace.bottom_row * height)

    return boundingBoxArray


  }

  displayFaceBox = (box) => {
    this.setState({box: box});
  }

  onInputChange = (event) => {
    this.setState({input: event.target.value})
  }

  onButtonSubmit = () => {
    this.setState({imageUrl: this.state.input})
    app.models.predict('e466caa0619f444ab97497640cefc4dc', 
                        this.state.input)
    .then(response => {
      console.log(response)
      if(response){
        console.log("aaa");
        console.log("ccc" + this.state.user.id);
        fetch('https://infinite-refuge-16426.herokuapp.com/image', {
          method: 'put',
          headers: {'Content-Type':'application/json'},
          body: JSON.stringify({
            id: this.state.user.id
        })
      })
      .then(response => response.json())
      .then(count => {
        this.setState(Object.assign(this.state.user, {entries:count}))
      })
      
    }
      this.displayFaceBox(this.calculateFaceLocation(response))
    
    })
    .catch(err => console.log(err)) 
  

  }

  onRouteChange = (route) =>{
    if (route ==='signout'){
      this.setState({isSignedIn: false})      
    } else if (route === 'home'){
      this.setState({isSignedIn: true})      
    }
    this.setState({route: route})
  }

  render(){
      return (
        <div className="App">
          <Particles  className='particles'
              params={ particlesOptions }             
            />


           <Navigation isSignedIn={this.state.isSignedIn} onRouteChange={this.onRouteChange}/>
           {  this.state.route === 'home' 
            ?  
            <div> 
              <Logo />
              <Rank name={this.state.user.name} entries={this.state.user.entries}/>
              <ImageLinkForm 
                onInputChange={this.onInputChange} 
                onButtonSubmit={this.onButtonSubmit}/>
              <FaceRecognition box={this.state.box} imageUrl={this.state.imageUrl}/>
            </div>
            : (
                this.state.route === 'signin'
                ? <Signin loadUser={this.loadUser}
                          onRouteChange={this.onRouteChange}/>
                : <Register loadUser={this.loadUser} onRouteChange={this.onRouteChange}/>
              )
            }
        </div>
  );
  }

}

export default App;
